@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@600&display=swap");

* {
  margin: 0;
  padding: 0;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  font-family: "Cairo", sans-serif;
  background-color: #36454f;
}
